.info {
    margin: 0 0 10px;
}
  
.item {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    padding: 10px 10px;
  
    border-radius: 5px;
    background-color: #ffffff;
    box-shadow: -1px 2px 10px 2px rgba(34, 60, 80, 0.2);
}
  
.editorInput input {
    display: block;
    margin-bottom: 10px;
    border: 2px solid #757575;
    border-radius: 7px;
    height: 30px;
    padding: 5px;
    outline: none;
}
  
.editorInput input:focus {
    border: 2px solid #26c6da;
}
  
.btnGroup {
    margin-left: auto;
}